.line__border,
.line__border-1,
.line__border-2,
.line__border-3,
.line__border-4 {
  position: relative;
  background-color: var(--light-color);
}

.line__border-wrapper {
  position: absolute;
}

@media (max-width: 990px) {
  .line__border {
    display: none;
  }
}
.line__border .line-secondary-main {
  content: "";
  width: 100%;
  height: 30px;
  background-color: var(--secondary-color);
}
@media (max-width: 1330px) {
  .line__border .line-secondary-main {
    height: 16px;
  }
}
.line__border .line-gray-main {
  content: "";
  width: 52%;
  height: 60px;
  background-color: white;
  transform: skew(-45deg, 0deg);
  transform-origin: top right;
}
@media (max-width: 1330px) {
  .line__border .line-gray-main {
    height: 32px;
  }
}
.line__border .line-primary-main {
  content: "";
  width: 65%;
  height: 30px;
  background-color: var(--primary-color);
  transform: skew(-45deg, 0deg);
  position: absolute;
  right: -16px;
  top: 30px;
}
@media (max-width: 1330px) {
  .line__border .line-primary-main {
    height: 16px;
    top: 16px;
  }
}

